import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/auth/components/SuccessNotificationForm/SuccessNotificationForm.css';
import { Button } from '@adobe/elsie/components';
import { SuccessNotificationFormProps } from '@/auth/types';

export const SuccessNotificationForm: FunctionComponent<
  SuccessNotificationFormProps & HTMLAttributes<HTMLDivElement>
> = ({
  formSize = 'default',
  className = '',
  headingText = '',
  messageText = '',
  primaryButtonText = '',
  secondaryButtonText = '',
  onPrimaryButtonClick,
  onSecondaryButtonClick,
}) => {
  if (!primaryButtonText && !secondaryButtonText) return null;

  return (
    <div
      className={classes(['auth-successNotificationForm', formSize, className])}
    >
      <p
        className="auth-successNotificationForm__title"
        data-testid="notification-title"
      >
        {headingText}
      </p>
      <p
        className="auth-successNotificationForm__content-text"
        data-testid="notification-content"
      >
        {messageText}
      </p>

      <Button
        type="button"
        className="auth-successNotificationForm__button auth-successNotificationForm__button--top"
        onClick={onPrimaryButtonClick}
      >
        {primaryButtonText}
      </Button>
      <Button type="button" variant="tertiary" onClick={onSecondaryButtonClick}>
        {secondaryButtonText}
      </Button>
    </div>
  );
};
